.future {
	background-image: radial-gradient(circle, #887e1b, #878417, #868a13, #839110, #80970d, #7d9613, #799619, #76951d, #728d25, #6e852b, #6a7d30, #667534);
	-webkit-background-size: cover;
	background-size: cover;
	padding-top: 52px;
	padding-bottom: 120px;
	position: relative;
	width: 100%;
	&__logo {
		text-align: center;
		position: relative;
		z-index: 2;
		height: 153px;
		background: url("../images/sprites/svg/logo.svg") center no-repeat;
		-webkit-background-size: contain;
		background-size: contain;
	}
	&__title {
		text-align: center;
		color: $whiteO;
		font-family: $font-ralB;
		font-size: 1.8vw;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 3px;
		margin-top: 40px;
		position: relative;
		z-index: 2;
	}
	&::before {
		content: url("../images/future/future-hous.png");
		background: url("../images/future/future-hous.png") right no-repeat;
		position: absolute;
		right: 0;
		bottom: 0;
	}
}
.photo {
	&__list {
		display: flex;
		flex-flow: wrap;
		li {
			width: 20%;
			min-height: 250px;
			position: relative;
			overflow: hidden;
		}
	}
	&__item {
		width: 100%;
		height: 100%;
		//max-height: 377px;
		transition: transform 0.6s;
		img {
			height: 100%;
			width: 100%;
			min-height: 250px;
			display: block;
		}
		&:hover {
			transform: scale(1.2);
		}
	}
}