.structure {
	padding-top: 100px;
	padding-bottom: 79px;
	&__title {
		color: #848484;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 3px;
		text-align: center;
	}
	&__descr {
		color: #848484;
		font-family: $font-ral;
		font-size: 24px;
		font-weight: 400;
		letter-spacing: 0.84px;
		text-align: center;
		margin-top: 20px;
	}
	&__wrap {
		margin-top: 118px;
	}
	&__list {
		display: flex;
		flex-flow: wrap;
	}
	&__item {
		display: flex;
		width: 33.3%;
		margin-bottom: 84px;
	}
	&__img {
		width: 91px;
		height: 91px;
		background-color: #b6d841;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 53px;
			height: 44px;
			transform: translate(-50%, -50%);
			-webkit-background-size: contain;
			background-size: contain;
		}
		&--transport::before {
			background: url("../images/sprites/svg/transport.svg") center no-repeat;
		}
		&--schoolbus::before {
			background: url("../images/sprites/svg/schoolbus.svg") center no-repeat;
		}
		&--store::before {
			background: url("../images/sprites/svg/store.svg") center no-repeat;
		}
		&--hospital::before {
			background: url("../images/sprites/svg/hospital.svg") center no-repeat;
		}
		&--stabling::before {
			background: url("../images/sprites/svg/stabling.svg") center no-repeat;
		}
		&--coffee::before {
			background: url("../images/sprites/svg/coffee.svg") center no-repeat;
		}
		&--workout::before {
			background: url("../images/sprites/svg/workout.svg") center no-repeat;
		}
		&--hockey::before {
			background: url("../images/sprites/svg/hockey.svg") center no-repeat;
		}
		&--tennis::before {
			background: url("../images/sprites/svg/tennis.svg") center no-repeat;
		}
		use {
			fill: $white;
		}
	}
	&__text {
		color: #9f9f9f;
		font-family: $font-ralB;
		font-size: 14px;
		font-weight: 700;
		line-height: 21px;
		letter-spacing: 0.49px;
		margin-left: 27px;
		display: flex;
		align-items: center;
	}
}