.plan {
	padding-top: 60px;
	padding-bottom: 350px;
	background: url("../images/forest-min.jpg") center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	position: relative;
	overflow: hidden;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: -200px;
		left: 41%;
		width: 82%;
		height: 100%;
		transform: translateX(-50%);
		background: url("../images/sprites/svg/roads-genplan.svg") center no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
	}
	&__wrap {
		display: flex;
		position: relative;
		z-index: 1;
	}
	&__block {
		width: 50%;
		display: flex;
		justify-content: flex-end;
	}
	&__genplan {
		width: 70%;
		transform: translateY(-200px);
		margin-right: 30px;
		position: relative;
		background: url("../images/genplan/genplan-bg-2.svg") center no-repeat;
	}
	&__compas {
		position: absolute;
		bottom: -20px;
		left: 0;
		width: 73px;
		height: 96px;
		background: url("../images/sprites/svg/plan-compas.svg") center no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
	}
	&__block-right {
		width: 50%;
		margin-top: 114px;
	}
	&__head {
		display: flex;
		justify-content: space-between;
		margin-bottom: 57px;
	}
	&__head-box {
		display: flex;
	}
	&__head-text {
		margin-right: 60px;
	}
	&__title {
		min-width: 150px;
		color: #3c3c3c;
		font-family: $font-ral;
		font-size: 16px;
		font-weight: 500;
		letter-spacing: 0.8px;
	}
	&__num {
		height: 26px;
		color: #3c3c3c;
		font-family: "Open Sans", sans-serif;
		font-size: 36px;
		font-weight: 400;
		letter-spacing: 1.8px;
		text-align: center;
		margin-top: 32px;
	}
	&__area {
		display: flex;
		flex-direction: column;
		span {
			margin-bottom: 28px;
			color: #3d3d3d;
			font-family: "Open Sans", sans-serif;
			font-size: 12px;
			font-weight: 400;
			letter-spacing: 0.6px;
			position: relative;
			&::before {
				content: '';
				position: absolute;
				left: -41px;
				top: 0;
				width: 21px;
				height: 21px;
				background-color: #8cbd6c;
			}
		}
		span:last-child {
			&::before {
				background-color: #c0dc75;
			}
		}
	}
	&__desc {
		display: flex;
		margin-bottom: 39px;
		padding: 30px 45px 30px 0;
		justify-content: space-between;
		background-color: $white;
		box-shadow: 4px 9px 13px rgba(65, 56, 56, 0.1);
		margin-left: 30px;
		position: relative;
		z-index: 1;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: $white;
		}
	}
	&__arrow {
		position: absolute;
		left: -52px;
		top: 22px;
		height: 30px;
		width: 74px;
		display: block;
		z-index: -1;
		background: url("../images/sprites/svg/arrow-left.svg") center no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
		transform: rotate(-90deg);
	}
	&__desc-txt {
		position: relative;
		z-index: 1;
		color: #868686;
		font-family: $font-ral;
		font-size: 14px;
		font-weight: 500;
		span {
			margin-left: 19px;
			color: #868686;
			font-family: "Open Sans", sans-serif;
			font-size: 14px;
			font-weight: 400;
		}
	}
	&__desc-txt:first-child {
		span {
			color: #89b345;
			font-weight: 700;
		}
	}
	&__list {
		display: flex;
		flex-flow: wrap;
	}
	&__item {
		width: calc(50% - 23px);
		margin-bottom: 18px;
	}
	&__item:nth-child(odd) {
		margin-right: 23px;
	}
	&__item:nth-child(even) {
		margin-left: 23px;
	}
	&__item-a {
		display: flex;
		padding: 13px 34px;
		align-items: center;
		text-decoration: none;
		background-color: #efebe6;
		border-radius: 27px;
		box-shadow: 4px 9px 13px rgba(65, 56, 56, 0.1);
		color: #847567;
		font-family: $font-ral;
		font-size: 14px;
		font-weight: 400;
		transition: color 0.4s, background-color 0.4s;
		svg {
			width: 28px;
			height: 28px;
			fill: #847567;
			margin-right: 35px;
			transition: fill 0.4s;
		}
		&:hover {
			background-color: $greenL;
			color: $white;
			svg {
				fill: $white;
			}
		}
		&--active {
			background-color: $greenL;
			color: $white;
			svg {
				fill: $white;
			}
		}
	}
	&__food {
		display: flex;
		justify-content: flex-end;
		margin-top: 55px;
	}
	&__popup {
		position: absolute;
		top: 135px;
		left: 0;
		width: 100%;
		height: 85%;
		z-index: 100;
		visibility: hidden;
		opacity: 0;
		transition: visibility 0.4s, opacity 0.4s;
		&--active {
			visibility: visible;
			opacity: 1;
		}
		&-wrap {
			overflow: hidden;
			background-color: white;
			position: relative;
			z-index: 1;
		}
		&-close {
			position: absolute;
			top: 25px;
			right: 20px;
			width: 30px;
			height: 30px;
			z-index: 101;
			&::before{
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				width: 22px;
				height: 3px;
				background-color: rgba($white, 0.7);
				transform: rotate(45deg);
				border-radius: 10px;
				transition: background-color 0.4s;
			}
			&::after{
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				width: 22px;
				height: 3px;
				background-color: rgba($white, 0.7);
				transform: rotate(-45deg);
				border-radius: 10px;
				transition: background-color 0.4s;
			}
			&:hover {
				&::after {
					background-color: rgba($white, 1);
				}
				&::before {
					background-color: rgba($white, 1);
				}
			}
		}
		&-head {
			position: relative;
			width: 100%;
			min-height: 450px;
			z-index: 0;
			&::before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 103%;
				height: 100%;
				min-height: 700px;
				background: url("../images/genplan/genplan-popup.svg") center no-repeat;
				transform: translateX(-2%);
				-webkit-background-size: cover;
				background-size: cover;
			}
			svg {
				position: absolute;
				top: 0;
				left: 0;
				width: 101%;
				transform: translate(-1%, -6px);
			}
		}
		&-foot {
			position: relative;
			z-index: 2;
			display: flex;
			padding: 15px 50px 90px 70px;
			.plan__item-a {
				box-shadow: none;
			}
		}
		&-left {
			width: 50%;
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			padding-right: 53px;
			position: relative;
			z-index: 2;
			overflow: hidden;
		}
		&-right {
			width: 50%;
			position: relative;
			z-index: 2;
		}
		&-list {
			position: relative;
			width: 100%;
			height: 100%;
		}
		&-item {
			bottom: 0;
			right: -150%;
			display: flex;
			position: absolute;
			transition: right 0.8s;
			&--active {
				right: 0;
			}
		}
		&-title {
			color: #87786a;
			font-family: $font-ralB;
			font-size: 30px;
			font-weight: 400;
			margin-bottom: 28px;
		}
		&-subtitle {
			color: #87786a;
			font-family: $font-ralB;
			font-size: 18px;
			font-weight: 400;
			margin-bottom: 18px;
		}
		&-desc {
			color: #87786a;
			font-family: $font-ral;
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			max-width: 245px;
			margin-bottom: 42px;
		}
		&-square {
			color: #87786a;
			font-family: $font-ralB;
			font-size: 18px;
			font-weight: 700;
			span {
				font-size: 36px;
			}
		}
		&-svg {
			width: 110px;
			height: 210px;
			background: url("../images/hokkey.png") left no-repeat;
			-webkit-background-size: contain;
			background-size: contain;
			margin-right: 71px;
		}
	}
}
.plan__label {
	display: flex;
	flex-direction: row-reverse;
	margin-right: 18px;
}
.plan__label input {
	position: absolute;
	z-index: -1;
	opacity: 0;
	margin: 10px 0 0 20px;
}
.plan__txt {
	position: relative;
	padding: 0 0 0 60px;
	cursor: pointer;
	color: #847567;
	font-family: $font-ral;
	font-size: 14px;
	font-weight: 400;
	padding-right: 68px;
}
.plan__txt:before {
	content: '';
	position: absolute;
	top: -4px;
	right: 0;
	width: 51px;
	height: 21px;
	border-radius: 11px;
	border: 1px solid #c6beb6;
	background-color: #f7f5f2;
	transition: .2s;
	box-sizing: border-box;
}
.plan__txt:after {
	content: '';
	position: absolute;
	top: -1px;
	right: 27px;
	width: 19px;
	height: 15px;
	border-radius: 8px;
	background-color: #c6beb6;
	transition: right 0.2s, background-color 0.2s;
}
.plan__label input:checked + .plan__txt:after {
	right: 5px;
	background-color: #b2da48;
}

.plan__zip {
	position: absolute;
	width: 30px;
	height: 30px;
	background-color: #e6d410;
	border-radius: 50%;
	z-index: 2;
	left: 42.5%;
	top: 36%;
	opacity: 0;
	visibility: hidden;
	transition: visibility 0.4s, opacity 0.4s;
	&--2 {
		top: 43.3%;
	}
	&--3 {
		top: 50.7%;
	}
	&--4 {
		top: 58%;
	}
	&--5 {
		top: 65.3%;
	}
	&--6 {
		top: 72.5%;
	}
	&--7 {
		top: 79.8%;
	}
	&--8 {
		top: 87.3%;
	}
	&::after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 13px;
		height: 21px;
		background: url("../images/sprites/svg/zip.svg") center no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
	}
	&::before{
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 15px;
		height: 15px;
		background-color: #e6d410;
	}
}
.plan__zip--active {
	opacity: 1;
	visibility: visible;
}
