.comfort {
	background: -webkit-linear-gradient(top, rgb(126, 136, 83) 0%, rgb(100, 115, 48) 60%, rgb(107, 123, 50) 100%);
	background: -o-linear-gradient(top, rgb(126, 136, 83) 0%, rgb(100, 115, 48) 60%, rgb(107, 123, 50) 100%);
	background: -ms-linear-gradient(top, rgb(126, 136, 83) 0%, rgb(100, 115, 48) 60%, rgb(107, 123, 50) 100%);
	background: -moz-linear-gradient(top, rgb(126, 136, 83) 0%, rgb(100, 115, 48) 60%, rgb(107, 123, 50) 100%);
	background: linear-gradient(to bottom, rgb(126, 136, 83) 0%, rgb(100, 115, 48) 60%, rgb(107, 123, 50) 100%);
	-webkit-background-size: cover;
	background-size: cover;
	padding-top: 101px;
	padding-bottom: 92px;
	&__title {
		text-align: center;
		color: $white;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		letter-spacing: 3px;
		text-transform: uppercase;
	}
	&__descr {
		text-align: center;
		color: $white;
		font-family: $font-ral;
		font-weight: 400;
		font-size: 24px;
		letter-spacing: 2.4px;
		margin-top: 20px;
	}
	&__list {
		display: flex;
		flex-flow: wrap;
		margin-top: 40px;
		li {
			width: 33.3%;
		}
	}
	&__item {
		color: $white;
		display: flex;
		padding: 54px 0 54px 39px;
		position: relative;
		height: 73%;
		z-index: 3;
		svg {
			use {
				fill: $white;
			}
		}
	}
	&__subtitle {
		font-family: $font-ralB;
		font-size: 18px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.63px;
		padding-bottom: 11px;
		line-height: 33.75px;
	}
	&__par {
		font-family: $font-ral;
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		letter-spacing: 0.49px;
		&--active {
			position: absolute;
			font-family: $font-ral;
			font-size: 14px;
			font-weight: 400;
			letter-spacing: 0.49px;
			top: 96px;
			line-height: 24px;
			opacity: 0;
		}
		span {
			font-family: $font-ralB;
			font-weight: 700;
			font-size: 14px;
		}
	}
	&__items {
		display: flex;
		text-align: center;
		justify-content: space-between;
		position: relative;
		z-index: 4;
		li {
			width: 21.5%;
		}
	}
	&__item-two {
		width: 100%;
		min-height: 220px;
		position: relative;
		color: $white;
		cursor: pointer;
		overflow: hidden;
		z-index: 4;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: $white;
			opacity: 0.09;
		}
		.comfort__subtitle {
			height: 46px;
			display: flex;
			align-items: center;
		}
		&:hover {
			.comfort__wrapper {
				left: 100%;
			}
			.comfort__wrapper--hover {
				left: 0;
			}
		}
	}
	&__wrapper {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		transition: all 0.6s;
		&--hover {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			position: absolute;
			top: 0;
			height: 100%;
			left: -100%;
			width: 100%;
			transition: all 0.6s;
			.comfort__subtitle {
				height: auto;
			}
		}
	}
	&__price {
		color: $white;
		font-family: $font-ralB;
		font-size: 28px;
		font-weight: 700;
		line-height: 52.24px;
		letter-spacing: 0.87px;
		margin-top: 27px;
	}
	&__info {
		position: absolute;
		top: 8px;
		right: -35px;
		width: 19px;
		height: 19px;
		background: url("../images/sprites/svg/info.svg") center no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
	}
	&__svg {
		width: 78px;
		height: 127px;
		display: flex;
		justify-content: center;
		align-items: center;
		-webkit-background-size: contain;
		background-size: contain;
		&--utility {
			background: url("../images/sprites/svg/utility.svg") center no-repeat;
		}
		&--water-2 {
			background: url("../images/sprites/svg/water-2.svg") center no-repeat;
		}
		&--el-energy {
			background: url("../images/sprites/svg/el-energy.svg") center no-repeat;
		}
		&--registration {
			background: url("../images/sprites/svg/registration.svg") center no-repeat;
		}
	}
	&__img {
		width: 46px;
		height: 56px;
		margin-right: 56px;
		text-align: center;
		-webkit-background-size: contain;
		background-size: contain;
		opacity: 1;
		animation: bgOp 0.4s linear;
		&--roads {
			background-image: url("../images/sprites/svg/roads.svg");
			background-position: center;
			background-repeat: no-repeat;
		}
		&--energy {
			background: url("../images/sprites/svg/energy.svg") center no-repeat;
		}
		&--light{
			background: url("../images/sprites/svg/light.svg") center no-repeat;
		}
		&--water{
			background: url("../images/sprites/svg/water.svg") center no-repeat;
		}
		&--enternet{
			background: url("../images/sprites/svg/enternet.svg") center no-repeat;
		}
		&--protect{
			background-image: url("../images/sprites/svg/protect.svg");
			background-position: center;
			background-repeat: no-repeat;
		}
	}
}
.comfort__item--active {
	cursor: pointer;
	transition: color 0.4s, background-color 0.4s;
	.comfort__subtitle {
		position: relative;
		&::before {
			position: absolute;
			content: '';
			height: 1px;
			width: 99%;
			bottom: 8px;
			left: 0;
			border-bottom: 2px dotted $white;
		}
	}
	&:hover {
			background-color: $white;
			color: $green;
		.comfort__img--protect {
			background-image: url("../images/sprites/svg/protect-green.svg");
		}
		.comfort__img--roads {
			background-image: url("../images/sprites/svg/roads-green.svg");
		}
		.comfort__par {
			opacity: 0;
		}
		.comfort__par--active {
			opacity: 1;
			color: $black;
		}
	}
}
@keyframes bgOp {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}