.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 200;
	background-color: black;
	visibility: visible;
	opacity: 1;
	transition: opacity 1s, visibility 1s;
	.loader {
		width: 75px;
		height: 75px;
		border: 10px solid white;
		border-radius: 50%;
		border-top-color: $greenL;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		animation: 2s spin infinite linear;
	}
}
.done {
	visibility: hidden;
	opacity: 0;
}
@keyframes spin {
	from {
		transform: translate(-50%, -50%) rotate(0deg);
	}
	to {
		transform: translate(-50%, -50%) rotate(360deg);
	}
}